<template>
  <el-container class="register-container">
    <el-header class="register-header">
      <div style="margin:auto"></div>
    </el-header>
    <el-main class="register-main">
      <el-form
        ref="usernameRegister"
        :model="registerInfo"
        :rules="rules"
        label-suffix="left"
        label-width="0px"
      >  
        <el-row>
          <el-col :span="5">
            <div align="right" class="register-title"></div>
          </el-col>
          <el-col :span="19">
            <el-form-item prop="username" :error="registerErrUsrnm">
              <el-input
                type="text"
                v-model="registerInfo.username"
                placeholder=""
                auto-complete="off"
                prefix-icon="el-icon-user-solid"
              ></el-input>
              <div align="left" class="register-tip">5-30</div>
            </el-form-item>
          </el-col>
        </el-row>          
               
        <el-row>
          <el-col :span="5">
            <div align="right" class="register-title"></div>
          </el-col>
          <el-col :span="19">
            <el-form-item prop="enigma" :error="registerErrPswd">
              <el-input
                type="password"
                v-model="registerInfo.enigma"
                placeholder=""
                auto-complete="off"
                prefix-icon="el-icon-lock"
              ></el-input>
              <div align="left" class="register-tip">6-50</div>
            </el-form-item>
          </el-col>
        </el-row>
                   
        <el-row>
          <el-col :span="5">
            <div align="right" class="register-title"></div>
          </el-col>
          <el-col :span="19">
            <el-form-item prop="enigma2" :error="registerErrPswd2">
            <el-input
              type="password"
              v-model="registerInfo.enigma2"
              placeholder=""
              auto-complete="off"
              prefix-icon="el-icon-lock"
            ></el-input>
            <div align="left" class="register-tip"></div>
            </el-form-item>
          </el-col>
        </el-row>
      
        <el-row>
          <el-col :span="5">
            <div align="right" class="register-title"></div>
          </el-col>
          <el-col :span="19">
            <el-form-item prop="email" :error="registerErrEml">
              <el-input
                type="text"
                v-model="registerInfo.email"
                placeholder=""
                auto-complete="off"
                prefix-icon="el-icon-message"
              ></el-input>
              <div align="left" class="register-tip"></div>
            </el-form-item>
          </el-col>
        </el-row> 

        <el-row>
          <el-col :span="5">
            <div align="right" class="register-title"></div>
          </el-col>
          <el-col :span="19">
            <el-form-item prop="phone" :error="registerErrPhn">
              <el-input
                type="text"
                v-model="registerInfo.phone"
                placeholder=""
                auto-complete="off"
                prefix-icon="el-icon-phone-outline"
              ></el-input>
              <div align="left" class="register-tip"></div>
            </el-form-item>
          </el-col>
        </el-row>      
      </el-form>

      <el-row>
        <el-col :span="12">
          <el-button type="primary" @click="register" style="width: 60%"></el-button>
        </el-col>
        <el-col :span="12">
          <el-button @click="goLogin" style="width: 60%"></el-button>
        </el-col>
      </el-row>      
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'Register',
  data() {
    let pswd_validator = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    let pswd2_validator = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        if (value !== this.registerInfo.enigma) {
          callback(new Error(''))
        }
        callback()
      }
    }
    let email_validator = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        let expression = /^([a-zA-Z0-9]+[-_\.]?)+@([a-zA-Z0-9]+\.)+[a-z]+$/
        if (!expression.test(value)) {
          callback(new Error(''))
        }
        callback()
      }
    }
    let phone_validator = (rule, value, callback) => {
      if(!value) {
        callback(new Error(''))
      } else {
        let expression = /^[0-9]{11}$/
        if(!expression.test(value)) {
          callback(new Error(''))
        }
        callback()
      }
    }
    return {
      registerInfo: {
        username: '',
        enigma: '',
        enigma2: '',
        email: '',
        phone: ''
      },
      registerErrUsrnm: '',
      registerErrPswd: '',
      registerErrPswd2: '',
      registerErrEml: '',
      registerErrPhn: '',
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { max: 30, message: '30', trigger: 'blur' },
          { min: 5, message: '5', trigger: 'blur' }
        ],
        enigma: [
          { validator: pswd_validator, trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        enigma2: [{ validator: pswd2_validator, trigger: 'blur' }],
        email: [
          { validator: email_validator, trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' }
        ],
        phone :[
          { validator: phone_validator, trigger: 'blur'},
        ]
      }
    }
  },
  methods: {
    register() {
      this.$refs['usernameRegister'].validate(valid => {
        if (valid) {
          this.registerErrEml = ''
          this.registerErrUsrnm = ''
          this.registerErrPswd = ''
          this.registerErrPswd2 = ''
          this.registerErrPhn = ''
          let params = new URLSearchParams()
          params.append('username', this.registerInfo.username)
          params.append('password', this.registerInfo.enigma)
          params.append('email', this.registerInfo.email)
          params.append('phone', this.registerInfo.phone)
          this.$axios
            .post('/accounts/register', params)
            .then(response => {
              this.$alert('','',{
                confirmButtonText: '',
                callback: action=>{
                  this.$router.push('/')
                }
              })            
            })
            .catch(err => {
              this.$message.error('')
              console.log(err)
              if (err.data == 'Username already exists') {
                console.log('reg fail1')
                this.registerErrUsrnm = ''
              }
              else if ('email' in err.data){
                this.registerErrEml = ''
              }
            })
        }
      })
    },
    goLogin(){
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.register-container {
  border-radius: 5px;
  background-clip: padding-box;
  margin: 100px auto;
  padding: 20px;
  width: 550px;
  border: 1px solid silver;
  font-weight: 600;
  font-size: 25px;
}
.register-header {
  display: flex;
  text-align: center;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid silver;
}
.register-main {
  font-weight: 400;
  font-size: 18px;
}
.register-title {
  font-weight: 400;
  font-size: 18px;
  vertical-align: middle;
  line-height: 40px;
}
.register-tip {
  font-weight: 400;
  font-size: 16px;
  color: silver;
  margin-bottom: 0;
  padding: 0;
  line-height: 22px;
}
</style>



